
import React from 'react';
import { TimelineTask } from '../../types/timeline.types';
import { format } from 'date-fns';
import { Slider } from '../../primitives/Slider';
import { motion } from 'framer-motion';

export const TaskDetailSidebar: React.FC<{ task: TimelineTask; onClose: () => void; onSave: (u: Partial<TimelineTask>) => void }> = ({
  task,
  onClose,
  onSave
}) => {
  const [title, setTitle] = React.useState(task.title);
  const [progress, setProgress] = React.useState(task.progress);

  return (
    <motion.aside initial={{ x: 300 }} animate={{ x: 0 }} exit={{ x: 300 }} transition={{ type: 'spring' }} className="w-80 p-4 bg-white shadow-lg">
      <div className="flex justify-between items-center mb-3">
        <h3 className="text-lg font-semibold">Task</h3>
        <button onClick={onClose} aria-label="Close">Close</button>
      </div>
      <label className="block text-sm">Title</label>
      <input value={title} onChange={(e) => setTitle(e.target.value)} className="w-full border rounded px-2 py-1 mb-3" />
      <div className="text-sm text-neutral-600 mb-2">{format(task.startDate, 'MMM d, yyyy')} â€” {format(task.endDate, 'MMM d, yyyy')}</div>
      <label className="block text-sm">Progress: {progress}%</label>
      <Slider value={progress} onChange={setProgress} />
      <div className="flex gap-2 mt-4">
        <button onClick={() => onSave({ title, progress })} className="px-3 py-1 bg-primary-500 text-white rounded">Save</button>
        <button onClick={onClose} className="px-3 py-1 border rounded">Cancel</button>
      </div>
    </motion.aside>
  );
};
