"use client";
import React, { useMemo, useRef, useState } from 'react';
import { TimelineViewProps } from '../../types/timeline.types';
import { calculateLeft, calculateWidth } from '../../utils/position.utils';
import { TimelineGrid } from '../Timeline/TimelineGrid';
import { TaskBar } from '../Timeline/TaskBar';
import { DependencyLine } from '../Timeline/DependencyLine';
import { TaskDetailSidebar } from '../Timeline/TaskDetailSidebar';

const LEFT_PANEL_WIDTH = 220;
const ROW_HEIGHT = 64;

export const TimelineView: React.FC<TimelineViewProps> = ({ rows, tasks, startDate, endDate, viewMode, onTaskUpdate }) => {
  const pixelsPerDay = viewMode === 'day' ? 40 : viewMode === 'week' ? 20 : 10;
  const totalDays = Math.ceil((endDate.getTime() - startDate.getTime()) / (1000 * 60 * 60 * 24));
  const timelineWidth = Math.max(800, totalDays * pixelsPerDay + 200);
  const containerRef = useRef<HTMLDivElement | null>(null);
  const [selectedTask, setSelectedTask] = useState<string | null>(null);

  const positions = useMemo(() => {
    const map = new Map<string, { left: number; width: number; top: number }>();
    rows.forEach((row, ri) => {
      row.tasks.forEach((tid) => {
        const t = tasks[tid];
        if (!t) return;
        const left = calculateLeft(t.startDate, startDate, pixelsPerDay);
        const width = calculateWidth(t.startDate, t.endDate, pixelsPerDay);
        const top = ri * ROW_HEIGHT + 12;
        map.set(tid, { left, width, top });
      });
    });
    return map;
  }, [rows, tasks, startDate, pixelsPerDay]);

  const depPaths = useMemo(() => {
    const arr: JSX.Element[] = [];
    Object.values(tasks).forEach((t) => {
      t.dependencies?.forEach((depId) => {
        const from = positions.get(depId);
        const to = positions.get(t.id);
        if (!from || !to) return;
        const x1 = from.left + from.width + LEFT_PANEL_WIDTH;
        const y1 = from.top + 48;
        const x2 = to.left + LEFT_PANEL_WIDTH;
        const y2 = to.top + 48;
        arr.push(<DependencyLine key={`${depId}-${t.id}`} x1={x1} y1={y1} x2={x2} y2={y2} />);
      });
    });
    return arr;
  }, [tasks, positions]);

  const commitPosition = (taskId: string, left: number, width: number) => {
    const days = Math.round(left / pixelsPerDay);
    const newStart = new Date(startDate);
    newStart.setDate(newStart.getDate() + days);
    const durDays = Math.max(1, Math.round(width / pixelsPerDay));
    const newEnd = new Date(newStart);
    newEnd.setDate(newEnd.getDate() + durDays);
    onTaskUpdate(taskId, { startDate: newStart, endDate: newEnd });
  };

  return (
    <div className="relative bg-white border rounded" style={{ minHeight: rows.length * ROW_HEIGHT + 80 }}>
      <div style={{ display: 'flex' }}>
        <div style={{ width: LEFT_PANEL_WIDTH, borderRight: '1px solid #e6edf3', background: '#fff', position: 'sticky', left: 0, zIndex: 6 }}>
          <div style={{ height: 48, display: 'flex', alignItems: 'center', paddingLeft: 12 }}><strong>Resources</strong></div>
          {rows.map((r) => <div key={r.id} style={{ height: ROW_HEIGHT, display: 'flex', alignItems: 'center', paddingLeft: 12, borderBottom: '1px solid #f3f4f6' }}>{r.label}</div>)}
        </div>

        <div ref={containerRef} style={{ overflowX: 'auto', width: '100%' }}>
          <div style={{ position: 'relative', width: timelineWidth }}>
            <div style={{ position: 'sticky', top: 0, zIndex: 5, background: '#fff' }}>
              <div style={{ height: 48, position: 'relative', marginLeft: LEFT_PANEL_WIDTH }}>
                <TimelineGrid startDate={startDate} endDate={endDate} viewMode={viewMode} pixelsPerDay={pixelsPerDay} />
                <div style={{ position: 'absolute', left: calculateLeft(new Date(), startDate, pixelsPerDay), top: 0, bottom: 0, width: 2, background: 'rgba(239,68,68,0.9)' }} />
              </div>
            </div>

            <div style={{ position: 'relative' }}>
              <svg style={{ position: 'absolute', left: LEFT_PANEL_WIDTH, top: 48, width: timelineWidth, height: rows.length * ROW_HEIGHT }} aria-hidden>
                <defs>
                  <marker id="arrowhead" markerWidth="10" markerHeight="10" refX="9" refY="3" orient="auto">
                    <polygon points="0 0, 10 3, 0 6" fill="#94a3b8" />
                  </marker>
                </defs>
                <g>{depPaths}</g>
              </svg>

              <div style={{ marginLeft: LEFT_PANEL_WIDTH, paddingTop: 48 }}>
                {rows.map((row) => (
                  <div key={row.id} style={{ height: ROW_HEIGHT, position: 'relative', borderBottom: '1px solid #f3f4f6' }}>
                    {row.tasks.map((tid) => {
                      const t = tasks[tid];
                      if (!t) return null;
                      const pos = positions.get(tid)!;
                      return (
                        <TaskBar
                          key={t.id}
                          task={t}
                          left={pos.left}
                          width={pos.width}
                          top={pos.top}
                          onOpen={(id) => setSelectedTask(id)}
                          onCommit={(id, l, w) => commitPosition(id, l, w)}
                          pixelsPerDay={pixelsPerDay}
                        />
                      );
                    })}
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>

      {selectedTask && tasks[selectedTask] && (
        <div style={{ position: 'absolute', right: 0, top: 0 }}>
          <TaskDetailSidebar task={tasks[selectedTask]} onClose={() => setSelectedTask(null)} onSave={(u) => { onTaskUpdate(selectedTask, u); setSelectedTask(null); }} />
        </div>
      )}
    </div>
  );
};
